<template>
  <el-col :span="20">
    <div class="top">
      <el-row class="ul">
        <el-col :span="4" v-for="(item,index) in arr" :key="index" class="li">
          <p>{{item.title}}</p>
          <span>{{item.num}}</span>
        </el-col>
      </el-row>
    </div>
    <div class="tubiao">
      <qu-xian></qu-xian>
      <zhu-tu></zhu-tu>
    </div>
  </el-col>
</template>

<script>
  import quXian from './quXianZheXian'
  import zhuTu from './fenZuZhuTu'
    export default {
        name: "shuJuKeShiHua",
      components:{
          'qu-xian':quXian,
          'zhu-tu':zhuTu,
      },
      data(){
          return {
            arr:[
              {title:'观影人数',num:'12K'},
              {title:'阅读人数',num:'25K'},
              {title:'到场人数',num:'3471'},
              {title:'今日登录人数',num:'1539'},
              {title:'今日注册人数',num:'368'},
            ],
          }
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.top{
  .ul{
    overflow: hidden;
    padding: 1.5%;
    .li{
      overflow: hidden;
      display: inline-block;
      background-color: #fff;
      float: left;
      padding: 20px 2.15%;
      margin-right: 1%;
      color: #666;
      .border();
      &:last-child{
        margin-right: 0;
      }
      span{
        font-weight: 400;
        font-size: 18px;
        color: @mainColor;
      }
    }
  }
}
.tubiao{
  overflow: hidden;
}
</style>



