<template>
    <el-col :span="24" class="box">
      <div class="details">
        <div class="fanhui" @click="fanhui"><span class="el-icon-arrow-left"></span>返回</div>
        <div class="neirong">
          <h6>{{returnBlogDetails.title}}</h6>
          <el-row class="userTime">
            <div class="user">{{returnBlogDetails.username}}</div>
            <div class="time">{{returnBlogDetails.createdAt}}</div>
          </el-row>
          <div class="text" v-html="returnBlogDetails.desc"></div>
          <el-row class="biaoqian">
            <ul>
              <li v-for="(biaoqian,index) in returnBlogDetails.biaoqian" :key="index" :style="{color:biaoqian.color, borderColor:biaoqian.color}">{{biaoqian.title}}</li>
            </ul>
          </el-row>
        </div>
      </div>
    </el-col>
</template>

<script>
    export default {
        name: "blogDetails",
      methods:{
          fanhui(){
            this.$router.push({name:'content'})
          },
      },
      computed:{
          returnBlogDetails(){
            return this.$store.getters.getBlogDetails;
          }
      },
      beforeRouteEnter(to,from,next){
        next(vm => {
          if (!vm.$store.getters.getBlogDetails) {
            vm.fanhui();
          }
        })
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.box{
  background-color: @bjColor;
  overflow: scroll;
  width: 100%;
  height: calc(~"(100% - 61px)");
  .details{
    width: 80%;
    background-color: #fff;
    .border();
    margin: 20px auto;
    text-align: center;
    position: relative;
    .fanhui{
      position: absolute;
      top: 20px;
      left: 30px;
      cursor: pointer;
      color: #91a8b0;
      transition: color .5s;
      &:hover{
        color: #354c54;
      }
    }
    .neirong{
      padding: 30px 5%;
      h6{
        font-size: 16px;
        color: #354c54;
      }
      .userTime{
        color: #a2afb3;
        font-size: 12px;
        display: inline-block;
        padding-top: 10px;
        div{
          float: left;
        }
        .user{
          padding-right: 25px;
        }
      }
      .text{
        margin-top: 20px;
        border-radius: 2px;
        background-color: #f0f2f3;
        text-align: left;
        padding: 20px 20px;
        color: #354c54;
      }
    }
    .biaoqian{
      text-align: left;
      p{
        color: #33484f;
      }
      ul{
        padding: 15px 0 0;
        li{
          color: #d1dbde;
          display: inline-block;
          border: 1px solid #d1dbde;
          border-radius: 3px;
          padding: 2px 15px;
          font-size: 12px;
          cursor: pointer;
          margin-right: 15px;
        }
      }
    }
  }
}
</style>
<style lang="less">
  .text{
    .ql-syntax{
      background-color: #d7dadc;
      color: #131e22;
      overflow: visible;
      padding: 2px 5px;
      border-radius: 3px;
    }
  }
</style>



