<template>
    <div class="login">
      <div class="left">
        <div class="logo"><span></span><p>LOGO</p></div>
        <p class="fanhui" @click="fanHui"><span class="el-icon-arrow-left"></span>返回主页面</p>
      </div>
      <div class="right">
        <div><img src="../../assets/img/login_04.png" @mousedown="tuoDon"></div>
        <div class="time">{{time}}</div>
      </div>
      <transition name="slide-fade">
        <deng-lu v-if="!isRegistered"></deng-lu>
      </transition>
      <transition name="slide-fade">
        <registered v-if="isRegistered"></registered>
      </transition>
    </div>
</template>

<script>
  import Registered from './registered'
  import dengLu from './dengLu'
    export default {
        name: "login",
      components:{
          Registered,
        dengLu,
      },
      data(){
          return {
            time: '',
          }
      },
      methods:{
          fanHui(){
            this.$store.commit('setLoginShow',false);
          },
        tuoDon(e){
            e.preventDefault();
        },
      },
      computed:{
          isRegistered(){
            return this.$store.getters.getRegistered;
          },
      },
      created(){
          let timeDate = new Date();
          this.time = timeDate.getFullYear() + '-' + (timeDate.getMonth() + 1) + '-' + timeDate.getDate();
      },

    }
</script>

<style scoped lang="less">
 img{
   width: 100%;
 }
.login{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(0,0,0,.2);
  .left,
  .right{
    width: 50%;
    float: left;
    height: 100%;
    position: relative;
  }
  .left{
    background: url("../../assets/img/login_01.png") no-repeat;
    .logo{
      color: #fff;
      padding: 1.8% 3%;
      span{
        background: url("../../assets/img/logo.png") no-repeat;
        -webkit-background-size: 100% 100%;
        background-size: 100% 100%;
        display: inline-block;
        width: 22px;
        height: 22px;
        vertical-align: -20%;
      }
      p{
        font-size: 20px;
        display: inline;
        padding-left: 8px;
        line-height: 22px;
        opacity: .9;
        letter-spacing: 3px;
      }
    }
  }
  .right{
    background-color: #fff;
    div:nth-child(1){
      width: 25%;
      position: absolute;
      right: 0;
      top: 25%;
    }
    .time{
      position: absolute;
      bottom: 1.8%;
      right: 3%;
      color: #8d9aa6;
    }
  }
  .fanhui{
    color: #fff;
    cursor: pointer;
    opacity: .9;
    display: inline;
    position: absolute;
    bottom: 1.8%;
    left: 3%;
  }
}
.slide-fade-enter-active ,.slide-fade-leave-active{
 transition: all .5s ease;
}
.slide-fade-enter, .slide-fade-leave-to
 /* .slide-fade-leave-active for below version 2.1.8 */ {
 transform: translateX(20px);
 opacity: 0;
}
</style>



