<template>
  <div class="blog">
    <div class="newBlog">
      <p class="title">新建</p>
      <el-form :model="blog" :rules="rules" ref="blog">
        <el-form-item prop="title">
          <el-input placeholder="请输入标题" v-model="blog.title" clearable></el-input>
        </el-form-item>
        <quill-editor v-model="blog.desc" @change="onEditorChange($event)" :options="editorOption"></quill-editor>
        <!--<el-input type="textarea" :rows="5" placeholder="请输入内容" :model="blog.desc"></el-input>-->
      </el-form>
      <el-row class="biaoqian">
        <p>选择标签：</p>
        <ul>
          <li v-for="(item,index) in biaoqian" @click="item.active = !item.active" :style="{color:(item.active?item.color:''),borderColor:(item.active?item.color:'')}">{{item.title}}</li>
        </ul>
      </el-row>
      <el-button size="small" @click="fanHui">取消</el-button>
      <el-button type="primary" size="small" @click="submitForm('blog')">确定</el-button>
    </div>
  </div>
</template>

<script>
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
  import { quillEditor } from 'vue-quill-editor'

  const toolbarOptions = [
    ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
    ['code-block'],
    [{'header': 1}, {'header': 2}],               // custom button values
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
    [{'direction': 'rtl'}],                         // text direction
    [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
    [{'header': [1, 2, 3, 4, 5, 6, false]}],
    [{'color': []}, {'background': []}],          // dropdown with defaults from theme
    [{'font': []}],
    [{'align': []}],
    ['link', 'image'],
    ['clean']

  ];

  export default {
        name: "newBlog",
    components:{
      quillEditor,
    },
      data(){
          return {
            blog:{
              title: '',
              desc: '',
              text: '',
              username: '',
              biaoqian: [],
            },
            editorOption:{
              placeholder: '在这里输入内容！',
              theme: 'snow',  // or 'bubble'
              modules: {
                toolbar: {
                  container: toolbarOptions,
                },
              },
            },
            rules: {
              title: [{ required: true, message: '请输入标题名称', trigger: 'blur' },],
            },
            biaoqian: [
              {title: 'Vue', color: '#71d189', active: false},
              {title: 'HTML', color: '#f5956c', active: false},
              {title: 'CSS', color: '#f5c47a', active: false},
              {title: 'JavaScript', color: '#a683eb', active: false},
              {title: 'ES6', color: '#41ccba', active: false},
              {title: '前端', color: '#65b3f0', active: false},
              {title: '其它', color: '#496169', active: false},
            ],
          }
      },
      methods:{
          fanHui(){
            this.$confirm('已编辑的内容将不会保存，是否继续返回？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.$router.push({name:'content'})
            }).catch(() => {})
          },
        onEditorChange({editor, html, text}) {//内容改变事件
          //console.log("---内容改变事件---");
          this.blog.desc = html;
          this.blog.text = text;
          //console.log(html)
        },
        submitForm(blog) {
          this.$refs[blog].validate((valid) => {
            if (valid) {
              this.blog.username = this.$store.getters.getUser.username;
              this.biaoqian.forEach((item) => {
                if (item.active === true){
                  this.blog.biaoqian.push(item);
                }
              });
              const query = Bmob.Query('blog');
              let arr = ['title','desc','text','username','biaoqian'];
              let newBlog = this.$store.getters.getBlogDetails;
              arr.forEach(item => {
                query.set(item,this.blog[item]);
              });
              // query.set("title",this.blog.title);
              // query.set("desc",this.blog.desc);
              // query.set("text",this.blog.text);
              // query.set("username",this.blog.username);
              // query.set("biaoqian",this.blog.biaoqian);
              if (newBlog){
                query.set('id',newBlog.objectId)
              }
              query.save().then(res => {
                this.$message({
                  message: '发布成功！',
                  type: 'success',
                });
                this.$store.getters.getBlog.load = true;
                this.$router.push({name: 'content'})
              }).catch(err => {
                this.$message({
                  message: err.error,
                  type: 'error',
                })
              })
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
      },
    mounted(){
      let blog = this.$store.getters.getBlogDetails;
      if (blog){
        let biaoqian = blog.biaoqian;
        console.log(blog)
        this.blog.desc = blog.desc;
        this.blog.title = blog.title;
        this.biaoqian.forEach(item => {
          biaoqian.forEach(item2 => {
            if (item.title === item2.title){
              item.active = true;
            }
          })
        })
      }
    },
    beforeRouteEnter(to,from,next){
      if (localStorage.getItem("bmob")){
        next();
      } else {
        next(vm => {
          vm.$router.push(to.matched[0].path)
          vm.$message({
            message: '登录后才能发布博客！',
            type: 'warning',
          })
        })
      }
    },
    beforeRouteLeave(to,from,next){
      this.$store.commit('setBlogDetails','');
      next();
    },
    }
</script>

<style scoped lang="less">
@import "../../assets/css/less";
.blog{
  background-color: @bjColor;
  overflow: scroll;
  width: 100%;
  height: calc(~"(100% - 61px)");
  .newBlog{
    width: 80%;
    overflow: hidden;
    margin: 20px auto;
    background-color: #fff;
    .border();
    text-align: center;
    padding-bottom: 30px;
    .title{
      color: #131e22;
      font-size: 16px;
      font-weight: 500;
      padding-top: 30px;
    }
    .el-form{
      padding: 20px 5% 20px;
    }
    .el-button{
      padding: 9px 35px;
    }
    .el-button+.el-button{
      margin-left: 25px;
    }
    .biaoqian{
      text-align: left;
      padding-left: 5%;
      padding-bottom: 20px;
      p{
        color: #33484f;
      }
      ul{
        padding: 10px 0;
        li{
          color: #d1dbde;
          display: inline-block;
          border: 1px solid #d1dbde;
          border-radius: 3px;
          padding: 4px 18px;
          font-size: 12px;
          cursor: pointer;
          margin-right: 15px;
        }
      }
    }
  }
}
</style>
<style lang="less">
  .el-form-item.is-success .el-input__inner{
    border-color: #dcdfe6;
  }
</style>



