<template>
    <div class="open">
      <div class="openButton" @click="dialogFormVisible = true"><span class="el-icon-plus"></span>添加</div>
      <el-dialog title="新增" :visible.sync="dialogFormVisible" width="30%" class="openDiv" top="10vh">
        <el-form :model="form">
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off" required></el-input>
          </el-form-item>
          <el-form-item label="地址" :label-width="formLabelWidth">
            <el-input v-model="form.address" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话" :label-width="formLabelWidth">
            <el-input v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="日期" :label-width="formLabelWidth">
            <el-date-picker v-model="form.date" type="date" placeholder="选择日期" style="width: 100%"></el-date-picker>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="pushTable">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "addOpen",

      data(){
          return {
            dialogTableVisible: false,
            dialogFormVisible: false,
            form: {
              name: '',
              address: '',
              date: '',
              phone: '',
            },
            formLabelWidth: '60px'
          }
      },
      methods:{
          pushTable(){
            for (let i in this.form){
              if (!this.form[i]) {
                this.$message({
                  showClose: true,
                  message: '内容不能为空！',
                  type: 'warning',
                  duration: 1000,
                });
                return;
              }
            }
            const query = Bmob.Query('table');
            query.save(this.form).then(res => {
              for (let i in this.form){
                this.form[i] = '';
              }
              query.count().then(res => {
                this.$store.commit('setTabLength',res);
              });
              this.$message({
                showClose: true,
                message: '添加成功！',
                type: 'success',
                duration: 1000,
              });
              this.$emit('addClick');
            }).catch(err => {
              console.log(err)
            });
            this.dialogFormVisible = false;
          },
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.open{
  float: left;
  .openDiv{
    div{
      padding-right: 12px;
    }
  }
  .openButton{
    color: #fff;
    cursor: pointer;
    margin-right: 15px;
    padding: 8px 20px;
    border-radius: 5px;
    transition: all .8s;
    &:nth-child(1){
      background-color: @mainColor;
      box-shadow: 0 2px 4px #0099ff47;
      &:hover{
        background-color: #0090f0;
      }
    }
    span{
      padding-right: 5px;
    }
  }
  input{
    width: 20%;
  }
}
</style>



