<template>
  <div class="blog">
    <div class="main">
      <fen-lei></fen-lei>
      <nei-rong></nei-rong>
    </div>
  </div>
</template>

<script>
  import blogFenLei from './blogFenLei'
  import blogNeiRong from './blogNeiRong'
    export default {
        name: "blogContent",
      components:{
        'fen-lei':blogFenLei,
        'nei-rong':blogNeiRong,
      },
    }
</script>

<style scoped lang="less">
@import "../../assets/css/less";
.blog{
  background-color: @bjColor;
  overflow: hidden;
  width: 100%;
  height: calc(~"(100% - 61px)");
.main{
  width: 80%;
  height: 100%;
  overflow: hidden;
  margin: 20px auto;
}
}
</style>



