<template>
    <div class="user">
      <el-col>
        <el-button type="primary" plain @click="loginShow" v-if="!returnUser">登录或注册</el-button>
        <div class="userXinXi" v-else>
          <div class="img"><img :src="(returnUser.img?returnUser.img:img)"></div>
          <el-dropdown trigger="click" @command="logOut">
          <span class="el-dropdown-link">
            {{returnUser.username}}<i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="0">修改</el-dropdown-item>
              <el-dropdown-item command="1">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-col>
      <transition name="fanhui">
        <login v-if="isLoginShow"></login>
      </transition>
    </div>
</template>

<script>
  import Login from './login'
    export default {
        name: "user",
      components:{
          Login,
      },
      data(){
          return {
            img: require('../../assets/img/touXiang/5.png'),
          }
      },
      methods:{
        loginShow(){
          this.$store.commit('setLoginShow',true);
        },
        logOut(command){
          if (command === '1'){
            this.$store.commit('setUser','');
            let fenlei = this.$store.getters.getBlogFenLei;
            fenlei.forEach(item => {
              if (item.active){
                if (item.type === '1'){
                  this.$store.commit('setBlog','')
                }
              }
            });
            localStorage.removeItem("bmob");
          } else if (command === '0'){
            this.$router.push({name:'user'})
          }
        },
      },
      computed:{
          isLoginShow(){
            return this.$store.getters.getLoginShow;
          },
          returnUser(){
            let user = this.$store.getters.getUser;
            if (user){
              return user;
            } else {
              return false;
            }
          },
      },
      created(){
        let current = Bmob.User.current();
        if (current) {
          this.$store.commit('setUser',current)
        }
      },
    }
</script>

<style scoped lang="less">
.user{
  float: right;
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -34/2px;
  .el-button{
    font-size: 12px;
    padding: 10px 20px;
    float: left;
  }
  .userXinXi{
    float: left;
    margin-top: -4px;
    padding-right: 20px;
    .img{
      width: 35px;
      height: 35px;
      background-color: #f5f5f5;
      border-radius: 50%;
      border: 4px solid #e6ecf1;
      overflow: hidden;
      float: left;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .el-dropdown{
      float: left;
      line-height: 43px;
      padding-left: 10px;
      color: #33484f;
      cursor: pointer;
      i{
        color: #95a4a9;
      }
    }
  }
}
.fanhui-enter-active, .fanhui-leave-active {
  transition: all .5s ease;
}
.fanhui-enter, .fanhui-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(100px);
  opacity: 0;
}
</style>



