import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state:{
    table:{},
    tabLength: 0,
    isLoginShow: false,
    isRegistered: false,
    user: '',
    //博客模块
    blog: '',
    blogDetails: '',
    blogLoading: false,
    blogFenLei: [
      {title: '全部', active: true,type: '0'},
      {title: '我发布的', active: false,type: '1'},
    ],
    //项目模块
    project: '',
    isProject: false,
  },
  getters:{
    getTable(state){
      return state.table;
    },
    getTabLength(state){
      return state.tabLength;
    },
    getLoginShow(state){
      return state.isLoginShow;
    },
    getRegistered(state){
      return state.isRegistered;
    },
    getUser(state){
      return state.user;
    },
    //博客模块
    getBlog(state){
      return state.blog;
    },
    getBlogDetails(state){
      return state.blogDetails;
    },
    getBlogLoading(state){
      return state.blogLoading;
    },
    getBlogFenLei(state){
      return state.blogFenLei;
    },
    //项目模块
    getProject(state){
      return state.project;
    },
    getIsProject(state){
      return state.isProject;
    },
  },
  mutations:{
    setTable(state,data){
      state.table = data;
    },
    pushTable(state,data){
      state.table.push(data);
    },
    setTabLength(state,data){
      state.tabLength = data;
    },
    setLoginShow(state,data){
      state.isLoginShow = data;
    },
    setRegistered(state,data){
      state.isRegistered = data;
    },
    setUser(state,data){
      state.user = data;
    },
    //博客模块
    setBlog(state,data){
      state.blog = data;
    },
    setBlogDetails(state,data){
      state.blogDetails = data;
    },
    setBlogLoading(state,data){
      state.blogLoading = data;
    },
    //项目模块
    setProject(state,data){
      state.project = data;
    },
    setIsProject(state,data){
      state.isProject = data;
    },
  },
})



