<template>
    <div class="dengLu" ref="element" v-loading="loading">
      <div class="main">
        <h6>登录</h6>
        <!--<div class="from">-->
          <!--<div><input type="text" placeholder="Email"><span class="iconfont icon-mail"></span></div>-->
          <!--<div><input type="text" placeholder="Password"><span class="iconfont icon-lock"></span></div>-->
        <!--</div>-->
        <el-form :model="ruleForm" :rules="rules2" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="username">
            <el-input v-model="ruleForm.username" placeholder="Name"></el-input>
            <span class="iconfont icon-people"></span>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" placeholder="Password" v-model="ruleForm.password" autocomplete="off"></el-input>
            <span class="iconfont icon-lock"></span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <!--<el-button @click="resetForm('ruleForm2')">重置</el-button>-->
          </el-form-item>
        </el-form>
        <!--<el-button type="primary" round>登录</el-button>-->
        <p>还没有账号？<span @click="toRegistered">前往注册</span></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "dengLu",
      data(){
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请输入密码'));
          } else {
            callback()
          }
        };
        var checkName = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('名称不能为空'));
          } else {
            callback()
          }
        };
          return {
            ruleForm:{
              username: '',
              password: '',
            },
            rules2: {
              password: [
                { validator: validatePass, trigger: 'blur' }
              ],
              username: [
                { validator: checkName, trigger: 'blur' }
              ],
            },
            loading: false,
          }
      },
      methods:{
          toRegistered(){
            this.$store.commit('setRegistered',true);
          },
        submitForm(formName) {
          this.loading = true;
          this.$refs[formName].validate((valid) => {
            if (valid) {
              Bmob.User.login(this.ruleForm.username,this.ruleForm.password).then(res => {
                this.$message({
                  message: '登录成功！',
                  type: 'success',
                });
                this.$store.commit('setUser',res);
                this.$store.commit('setLoginShow',false);
              }).catch(err => {
                this.$message({
                  message: err.error,
                  type: 'error',
                })
              });
              this.loading = false;
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
      },
      mounted(){
        var windowHeight = document.documentElement.clientHeight;
        var height = this.$refs.element.offsetHeight;
        this.$refs.element.style.top = (windowHeight - height)/2 + 'px'
      },
    }
</script>

<style scoped lang="less">
.dengLu{
  width: 28%;
  max-width: 520px;
  height: auto;
  background-color: #fff;
  z-index: 9;
  position: fixed;
  border-radius: 5px;
  top: 15%;
  left: calc(~"(50% - (28%/2))");
  box-shadow: 2px 2px 29px 5px rgba(10,62,116,.1);
  .main{
    width: 85%;
    margin: 0 auto;
    text-align: center;
    padding: 30px 0;
    .el-form{
      padding-top: 40px;
      .el-input{
        position: relative;
      }
      span {
        position: absolute;
        right: 5px;
        top: 10px;
        color: #ccd3d9;
        font-size: 22px;
        line-height: 22px;
        background-color: #fff;
      }
    }
    p{
      color: #b8c0c8;
      font-size: 12px;
      line-height: 20px;
      padding-top: 10px;
      span{
        color: #228dfc;
        cursor: pointer;
      }
    }
    .el-button{
      width: 100%;
      margin-top: 20px;
      box-shadow: 0 2px 8px 2px rgba(30,123,219,.2);
      border-radius: 100px;
    }
    .from{
      padding-top: 10%;
      div{
        position: relative;
        input{
          width: calc(~"(100% - 10px)");
          border: none;
          border-bottom: 1px solid #dee7f0;
          padding: 30px 5px 5px 5px;
          transition: all .5s;
          &:focus{
            border-bottom: 1px solid #409EFF;
          }
        }
      }
    }
    h6{
      font-size: 20px;
      color: #49637e;
      font-weight: 400;
      position: relative;
      &:after{
        content: '';
        width: 26px;
        height: 2px;
        border-radius: 5px;
        background-color: #218bf9;
        position: absolute;
        bottom: -8px;
        left: calc(~"(50% - 13px)");
      }
    }
  }
}
</style>
<style lang="less">
  .dengLu{
    .main{
      .el-input__inner{
        border-radius: 0;
        border-top: none;
        border-left: none;
        border-right: none;
        padding: 0 5px;
        height: 30px;
      }
      .el-form-item__error{
        padding-top: 0;
      }
    }
    .el-form-item.is-success .el-input__inner{
      border-color: #dcdfe6;
    }
  }
</style>



