<template>
    <div class="personalResume">
      <div class="box">
        <ul>
          <li v-for="(item,index) in project" :key="index" @click="clickItem(item)">
            <span class="el-icon-edit edit" @click.stop="editItem(item)"></span>
            <span class="el-icon-delete delete" @click.stop="deleteItem(item)"></span>
            <!--<div class="img"><img src=""></div>-->
            <div class="txt">
              <h6>{{item.title}}</h6>
              <p v-html="item.jianJie"></p>
              <p class="github">Github: {{item.github}}</p>
            </div>
          </li>
        </ul>
      </div>
      <add-project @getProject="getProject"></add-project>
      <web-project v-show="returnIsProject"></web-project>
      <!--<iframe src="https://ouyangzecheng.github.io/meiTuan/index.html" frameborder="0" width="375" height="667" class="chuan"></iframe>-->
    </div>
</template>

<script>
  import AddProject from './addProject'
  import WebProject from './webProject'
    export default {
        name: "PersonalResume",
      components:{
          'add-project':AddProject,
          'web-project':WebProject,
      },
      data(){
          return {
            project: '',
          }
      },
      computed:{
          returnIsProject(){
            return this.$store.getters.getIsProject;
          },
      },
      methods:{
        clickItem(item){
          this.$store.commit('setIsProject',true);
          this.$store.commit('setProject',item);
        },
        editItem(item){

        },
        deleteItem(item){
          let user = this.$store.getters.getUser;
          if (user.permissions === 'admin'){
            this.$confirm('确定要删除该内容吗？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              const query = Bmob.Query('project');
              query.destroy(item.objectId).then(res => {
                this.$message({
                  type: 'success',
                  message: '删除成功!',
                  duration: 2000,
                });
                this.getProject();
              }).catch(err => {
                this.$message.error(err);
              })
            })
          } else {
            this.$message({
              message: '您没有删除权限！',
              type: 'warning',
              duration: 2000,
            });
          }
        },
        getProject(){
          const query = Bmob.Query("project");
          query.find().then(res => {
            this.project = res.reverse();
          });
        },
      },
      created(){
        this.getProject();
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.personalResume{
  background-color: @bjColor;
  position: relative;
  overflow: scroll;
  width: 100%;
  height: calc(100% - 61px);
  .chuan{
    position: absolute;
    top: 20px;
    left: calc(50% - (375px/2));
  }
  .box{
    width: 1000px;
    margin: 10px auto;
    ul{
      overflow: hidden;
      li{
        background-color: #fff;
        .border();
        overflow: hidden;
        margin: 20px 10px;
        position: relative;
        padding: 20px;
        transition: all .5s;
        &:hover{
          box-shadow: 0 2px 12px 2px rgba(3,29,78,.08);
        }
        .img{
          width: 120px;
          height: 120px;
          background-color: #ccc;
          float: left;
        }
        .txt{
          width: 92%;
          float: left;
          padding-left: 10px;
          h6{
            color: #354c54;
            font-size: 16px;
            .text-overflow(1)
          }
          p{
            color: #667f87;
            padding-top: 5px;
            .text-overflow(3)
          }
          .github{
            padding-top: 10px;
            color: #839094;
            font-size: 12px;
            letter-spacing: 1px;
          }
        }
        .delete,
        .edit{
          position: absolute;
          right: 20px;
          top: 20px;
          font-size: 18px;
          cursor: pointer;
          color: #c4ced2;
          transition: color .5s;
          &:hover{
            color: #F56C6C;
          }
        }
        .edit{
          right: 60px;
          &:hover{
            color: #67C23A;
          }
        }
      }
    }
  }
}
</style>



