<template>
    <el-col :span="18" class="right" v-loading="returnBlogLoading">
      <div class="neirong" v-for="(item,index) in returnBlog" :key="index" @click="clickItem(item)">
        <h6>{{item.title}}</h6>
        <span class="el-icon-edit edit" @click.stop="editItem(item)"></span>
        <span class="el-icon-delete delete" @click.stop="deleteItem(item)"></span>
        <p v-html="item.text" class="text"></p>
        <el-row class="userTime">
          <div class="user">发布账号：{{item.username}}</div>
          <div class="time">发布时间：{{item.createdAt}}</div>
        </el-row>
        <el-row class="biaoqian">
          <ul>
            <li v-for="(biaoqian,index) in item.biaoqian" :key="index" :style="{color:biaoqian.color, borderColor:biaoqian.color}">{{biaoqian.title}}</li>
          </ul>
        </el-row>
      </div>
    </el-col>
</template>

<script>
    export default {
        name: "blogNeiRong",
      data(){
          return {

          }
      },
      methods:{
        clickItem(blog){
          this.$store.commit('setBlogDetails',blog);
          this.$router.push({name:'details'})
        },
        editItem(item){
          let username = this.$store.getters.getUser.username;
          if (username === item.username){
            this.$store.commit('setBlogDetails',item);
            this.$router.push({name:'newBlog'})
          } else {
            this.$message({
              message: '您不能修改其他账号发布的内容！',
              type: 'warning',
              duration: 2000,
            });
          }
        },
        deleteItem(item){
          let user = this.$store.getters.getUser;
          if (user.username === item.username || user.permissions === 'admin'){
            this.$confirm('确定要删除该内容吗？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              const query = Bmob.Query('blog');
              query.destroy(item.objectId).then(res => {
                let blog = this.$store.getters.getBlog;
                for (let i=0; i<blog.length; i++){
                  if (blog[i].objectId === item.objectId){
                    blog.splice(i,1);
                  }
                }
                this.$store.commit('setBlog',blog);
                this.$message({
                  type: 'success',
                  message: '删除成功!',
                  duration: 2000,
                })
              }).catch(err => {
                this.$message.error(err);
              });
            }).catch(() => {
            })
          } else {
            this.$message({
              message: '您不能删除其他账号发布的内容！',
              type: 'warning',
              duration: 2000,
            });
          }
        },
      },
      computed:{
          returnBlog(){
            return this.$store.getters.getBlog;
          },
        returnBlogLoading(){
            return this.$store.getters.getBlogLoading;
        },
      },
      created(){
        if (this.$store.getters.getBlog.load !== false){
          this.$store.commit('setBlogLoading',true);
          let blogFenLei = this.$store.getters.getBlogFenLei;
          blogFenLei.forEach(item => {
            if (item.active){
              if (item.type === '0'){
                const query = Bmob.Query("blog");
                query.find().then(res => {
                  res.load = false;
                  this.$store.commit('setBlog',res.reverse());
                  this.$store.commit('setBlogLoading',false);
                });
              } else if (item.type === '1'){
                const query = Bmob.Query("blog");
                query.containedIn("username", [this.$store.getters.getUser.username]);
                query.find().then(res => {
                  res.load = false;
                  this.$store.commit('setBlog',res.reverse());
                  this.$store.commit('setBlogLoading',false);
                });
              }
            }
          });
        }
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.right{
  height: 100%;
  overflow: scroll;
  padding-bottom: 20px;
  padding-right: 10px;
  .neirong{
    margin-left: 20px;
    background-color: #fff;
    .border();
    padding: 20px;
    margin-bottom: 20px;
    transition: all .5s;
    cursor: default;
    position: relative;
    &:hover{
      box-shadow: 0 2px 12px 2px rgba(3,29,78,.08);
    }
    h6{
      color: #354c54;
      font-size: 16px;
      .text-overflow(1);
      padding-right: 20px;
    }
    .delete,
    .edit{
      position: absolute;
      right: 20px;
      top: 20px;
      font-size: 18px;
      cursor: pointer;
      color: #c4ced2;
      transition: color .5s;
      &:hover{
        color: #F56C6C;
      }
    }
    .edit{
      right: 60px;
      &:hover{
        color: #67C23A;
      }
    }
    p{
      color: #667f87;
      padding-top: 5px;
    }
    .text{
      .text-overflow(2);
    }
    .userTime{
      padding-top: 10px;
      color: #a2afb3;
      font-size: 12px;
      div{
        float: left;
        padding-right: 30px;
      }
    }
    .biaoqian{
      text-align: left;
      p{
        color: #33484f;
      }
      ul{
        padding: 15px 0 0;
        li{
          color: #d1dbde;
          display: inline-block;
          border: 1px solid #d1dbde;
          border-radius: 3px;
          padding: 2px 15px;
          font-size: 12px;
          cursor: pointer;
          margin-right: 15px;
        }
      }
    }
  }
}
</style>



