<template>
    <div class="zhuCe" ref="zhuCe">
      <div class="main">
        <h6>注册</h6>
        <!--<div class="from">-->
          <!--<div><input type="text" placeholder="Name"><span class="iconfont icon-people"></span></div>-->
          <!--<div><input type="text" placeholder="Email"><span class="iconfont icon-mail"></span></div>-->
          <!--<div><input type="text" placeholder="Password"><span class="iconfont icon-lock"></span></div>-->
          <!--<div><input type="text" placeholder="Confirm Password"><span class="iconfont icon-lock"></span></div>-->
        <!--</div>-->
        <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" class="demo-ruleForm">
          <el-form-item prop="username">
            <el-input v-model="ruleForm2.username" placeholder="Name"></el-input>
            <span class="iconfont icon-people"></span>
          </el-form-item>
          <el-form-item prop="email">
            <el-input v-model="ruleForm2.email" placeholder="Email"></el-input>
            <span class="iconfont icon-mail"></span>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" placeholder="Password" v-model="ruleForm2.password" autocomplete="off"></el-input>
            <span class="iconfont icon-lock"></span>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input type="password" placeholder="Confirm Password" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
            <span class="iconfont icon-lock"></span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
            <!--<el-button @click="resetForm('ruleForm2')">重置</el-button>-->
          </el-form-item>
        </el-form>
        <!--<el-button type="primary" round>提交</el-button>-->
        <p>已有账号？<span @click="toLogin">前往登录</span></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "registered",
      data(){
        var checkName = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('名称不能为空'));
          } else {
            callback()
          }
        };
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请输入密码'));
          } else {
            if (this.ruleForm2.checkPass !== '') {
              this.$refs.ruleForm2.validateField('checkPass');
            }
            callback()
          }
        };
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入密码'));
          } else if (value !== this.ruleForm2.password) {
            callback(new Error('两次输入密码不一致!'));
          } else {
            callback()
          }
        };
        var checkEmail = (rule, value, callback) => {
          const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
          if (!value) {
            return callback(new Error("邮箱不能为空"))
          }
          setTimeout(() => {
            if (mailReg.test(value)) {
              callback()
            } else {
              callback(new Error("请输入正确的邮箱格式"))
            }
          }, 100)
        };
          return {
            ruleForm2: {
              password: '',
              checkPass: '',
              username: '',
              email: '',
            },
            rules2: {
              password: [
                { validator: validatePass, trigger: 'blur' }
              ],
              checkPass: [
                { validator: validatePass2, trigger: 'blur' }
              ],
              username: [
                { validator: checkName, trigger: 'blur' }
              ],
              email: [
                {validator: checkEmail, trigger: ['blur', 'change'],type: 'email',}
              ]
            }
          }
      },
      mounted(){
        this.$nextTick(function () {
          var windowHeight = document.documentElement.clientHeight;
          var height = this.$refs.zhuCe.offsetHeight;
          this.$refs.zhuCe.style.top = (windowHeight - height)/2 + 'px';
        })
      },
      methods:{
          toLogin(){
            this.$store.commit('setRegistered',false);
          },
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              Bmob.User.register(this.ruleForm2).then(res => {
                this.$message({
                  message: '注册成功！',
                  type: 'success',
                });
                this.$store.commit('setRegistered',false);
              }).catch(err => {
                this.$message({
                  message: err.error,
                  type: 'error',
                })
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
      },
    }
</script>

<style scoped lang="less">
.zhuCe{
  width: 28%;
  max-width: 520px;
  height: auto;
  background-color: #fff;
  z-index: 9;
  position: fixed;
  border-radius: 5px;
  top: 15%;
  left: calc(~"(50% - (28%/2))");
  box-shadow: 2px 2px 29px 5px rgba(10,62,116,.1);
  .main {
    width: 85%;
    margin: 0 auto;
    text-align: center;
    padding: 30px 0;
    .el-form{
      padding-top: 40px;
      .el-input{
        position: relative;
      }
      span {
        position: absolute;
        right: 5px;
        top: 10px;
        color: #ccd3d9;
        font-size: 22px;
        line-height: 22px;
        background-color: #fff;
      }
    }
    p {
      color: #b8c0c8;
      font-size: 12px;
      line-height: 20px;
      padding-top: 10px;
      span {
        color: #228dfc;
        cursor: pointer;
      }
    }
    .el-button {
      width: 100%;
      margin-top: 20px;
      box-shadow: 0 2px 8px 2px rgba(30, 123, 219, .2);
      border-radius: 100px;
    }
    .from {
      padding-top: 10%;
      div {
        position: relative;
        input {
          width: calc(~"(100% - 10px)");
          border: none;
          border-bottom: 1px solid #dee7f0;
          padding: 30px 5px 5px 5px;
          transition: all .5s;
          &:focus {
            border-bottom: 1px solid #409EFF;
          }
        }
      }
    }
    h6 {
      font-size: 20px;
      color: #49637e;
      font-weight: 400;
      position: relative;
      &:after {
        content: '';
        width: 26px;
        height: 2px;
        border-radius: 5px;
        background-color: #218bf9;
        position: absolute;
        bottom: -8px;
        left: calc(~"(50% - 13px)");
      }
    }
  }
}
</style>
<style lang="less">
.zhuCe{
  .main{
    .el-input__inner{
      border-radius: 0;
      border-top: none;
      border-left: none;
      border-right: none;
      padding: 0 5px;
      height: 30px;
    }
    .el-form-item__error{
      padding-top: 0;
    }
  }
  .el-form-item.is-success .el-input__inner{
    border-color: #dcdfe6;
  }
}
</style>



