<template>
  <el-col class="tubiao">
    <v-chart :forceFit="true" :height="height" :data="data" :scale="scale" :padding="[40,30,70,50]">
      <v-tooltip />
      <v-axis />
      <v-legend />
      <v-smooth-line position="month*temperature" color="city" shape="smooth" />
      <v-point position="month*temperature" color="city" shape="circle" />
    </v-chart>
  </el-col>
</template>

<script>
  const DataSet = require('@antv/data-set');

  const sourceData = [
    { month: 'Jan', Tokyo: 7.0, London: 3.9 },
    { month: 'Feb', Tokyo: 6.9, London: 4.2 },
    { month: 'Mar', Tokyo: 9.5, London: 5.7 },
    { month: 'Apr', Tokyo: 14.5, London: 8.5 },
    { month: 'May', Tokyo: 18.4, London: 11.9 },
    { month: 'Jun', Tokyo: 21.5, London: 15.2 },
    { month: 'Jul', Tokyo: 25.2, London: 17.0 },
    { month: 'Aug', Tokyo: 26.5, London: 16.6 },
    { month: 'Sep', Tokyo: 23.3, London: 14.2 },
    { month: 'Oct', Tokyo: 18.3, London: 10.3 },
    { month: 'Nov', Tokyo: 13.9, London: 6.6 },
    { month: 'Dec', Tokyo: 9.6, London: 4.8 },
  ];
  const dv = new DataSet.View().source(sourceData);
  dv.transform({
    type: 'fold',
    fields: ['Tokyo', 'London'],
    key: 'city',
    value: 'temperature',
  });
  const data = dv.rows;

  const scale = [{
    dataKey: 'month',
    min: 0,
    max: 1,
  }];
    export default {
        name: "quXianZheXian",
      data(){
          return {
            data,
            scale,
            height: 300,
          }
      },
      mounted(){
        let height = document.querySelector('body').clientHeight - 208;
        if (height > 280){
          this.height = 280;
        } else {
          this.height = height;
        }
      },
    }
</script>

<style scoped lang="less">
@import "../../assets/css/less";
.tubiao{
  background-color: #fff;
  margin: 0 1.5% 2% 1.5%;
  width: 47.75%;
  max-height: 290px;
  .border();
}
</style>



