<template>
  <transition name="fade">
    <div class="di" @click="closeWeb">
      <div :class="(returnProject.keHuDuan === 'Phone'?'box phone':'box web')" @click.stop="">
        <div class="guanbi el-icon-close" @click="closeWeb"></div>
        <iframe :src="returnProject.url" frameborder="0" class="frame" :width="(returnProject.keHuDuan === 'Phone'?'375':'100%')" height="100%"></iframe>
      </div>
    </div>
  </transition>
</template>

<script>
    export default {
        name: "webProject",
      computed:{
          returnProject(){
            return this.$store.getters.getProject;
          },
      },
      methods:{
        closeWeb(){
          this.$store.commit('setIsProject',false);
          setTimeout(() => {
            this.$store.commit('setProject','');
          },1000)
        },
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.di{
  .openBj();
  overflow: scroll;
  .box{
    background-color: #fff;
    height: auto;
    position: fixed;
    top: 20px;
    bottom: 20px;
    border: 2px solid #bec6d2;
    .guanbi{
      background-color: rgba(0,0,0,.5);
      position: absolute;
      top: 30px;
      right: 30px;
      font-size: 18px;
      color: #fff;
      padding: 10px;
      border-radius: 20px;
      cursor: pointer;
      transition: all .5s;
      &:hover{
        background-color: rgba(0,0,0,.7);
      }
    }
  }
  .phone{
    width: 375px;
    height: 667px;
    left: calc(50% - (375px/2));
    .guanbi{
      top: -15px;
      right: -20px;
    }
  }
  .web{
    width: 95%;
    left: calc(50% - (95%/2));
  }
}
  .fade-enter-active, .fade-leave-active {
    transition: all .5s ease;
  }
  .fade-enter, .fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



