      <back-end-tab></back-end-tab>
      <router-view></router-view>
    </div>
</template>

<script>
  import backEndTab from './backEndTab'
    export default {
        name: "backEndModule",
      components:{
          'back-end-tab':backEndTab,
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.module{
  height: calc(~"(100% - 61px)");
  max-height: calc(~"(100% - 61px)");
  background-color: @bjColor;
  overflow: hidden;
}
</style>



