<template>
    <el-col :span="20" class="bigBox">
      <div class="userInformation">
        <el-col :span="6" class="left">
          <div class="img"><img :src="(returnUser.img?returnUser.img:img)"></div>
          <span class="xiuGaiTouXiang" v-show="isInput" @click="xiuGaiClick">修改头像</span>
          <h6>{{returnUser.username}}</h6>
          <p v-show="!isInput">{{(returnUser.Introduction?returnUser.Introduction:'简介信息')}}</p>
          <el-col class="userInput" v-show="isInput">
            <el-input type="textarea" v-model="returnUser.Introduction" :autosize="{ minRows: 2, maxRows: 3}" maxlength="50" placeholder="请输入内容" resize="none"></el-input>
          </el-col>
          <el-button type="primary" size="small" @click="isInputShow">{{(isInput?'确认修改':'修改信息')}}</el-button>
        </el-col>
        <el-col :span="18" class="right">
          <el-col :span="24" class="box">
            <el-row>联系方式</el-row>
            <el-col :span="8">
              <span>邮箱</span>
              <p>{{returnUser.email}}</p>
            </el-col>
            <el-col :span="8">
              <span>联系电话</span>
              <p v-show="!isInput">{{returnUser.phone}}</p>
              <el-col class="userInput" v-show="isInput">
                <el-input size="mini" placeholder="请输入联系电话" v-model="returnUser.phone"></el-input>
              </el-col>
            </el-col>
            <el-col :span="8">
              <span>地址</span>
              <p v-show="!isInput">{{returnUser.diZhi}}</p>
              <el-col class="userInput" v-show="isInput">
                <el-input size="mini" placeholder="请输入地址" v-model="returnUser.diZhi"></el-input>
              </el-col>
            </el-col>
          </el-col>
          <el-col :span="24" class="box">
            <el-row>时间信息</el-row>
            <el-col :span="8">
              <span>更新时间</span>
              <p>{{returnUser.updatedAt}}</p>
            </el-col>
            <el-col :span="8">
              <span>注册时间</span>
              <p>{{returnUser.createdAt}}</p>
            </el-col>
          </el-col>
          <el-col :span="24" class="box">
            <el-row>标签
              <div class="biaoQianInput" v-show="isInput">
                <el-input placeholder="请输入内容" class="input-with-select" size="mini" v-model="biaoqian">
                  <el-button slot="append" @click="addBiaoQian">添加</el-button>
                </el-input>
              </div>
            </el-row>
            <el-col :span="24">
              <div class="biaoQian" v-for="(item,index) in returnUser.biaoqian">{{item}}<span class="el-icon-close" v-show="isInput" @click="deleteBiaoQian(index)"></span></div>
            </el-col>
          </el-col>
        </el-col>
      </div>
      <transition name="fade">
        <div class="xiuGaiOpen" v-show="imgOpenShow" @click="imgOpenShow = !imgOpenShow">
          <transition name="slide">
            <div class="xiuGaiBox" ref="imgOpen" v-show="imgOpenShow" @click.stop="">
              <p>选择一个你喜欢的头像！</p>
              <el-row>
                <ul ref="ul">
                  <li v-for="(img,index) in allImg" :key="index" @click="clickImg(img,index)"><img :src="img"></li>
                </ul>
              </el-row>
              <el-button type="primary" size="small" @click="queDinImg">确定</el-button>
              <el-button size="small" @click="imgOpenShow = !imgOpenShow">取消</el-button>
            </div>
          </transition>
        </div>
      </transition>
    </el-col>
</template>

<script>
    export default {
        name: "userInformation",
      data(){
          return {
            isInput: false,
            biaoqian: '',
            img: require('../../assets/img/touXiang/5.png'),
            allImg: {
              0: require('../../assets/img/touXiang/1.png'),
              1: require('../../assets/img/touXiang/2.png'),
              2: require('../../assets/img/touXiang/3.png'),
              3: require('../../assets/img/touXiang/4.png'),
              4: require('../../assets/img/touXiang/5.png'),
              5: require('../../assets/img/touXiang/6.png'),
              6: require('../../assets/img/touXiang/7.png'),
              7: require('../../assets/img/touXiang/8.png'),
              8: require('../../assets/img/touXiang/9.png'),
              9: require('../../assets/img/touXiang/10.png'),
            },
            imgsrc: '',
            imgOpenShow: false,
          }
      },
      methods:{
        isInputShow(){
          if (!localStorage.getItem("bmob")){
            this.$message({
              message: '当前未登录，不能修改信息！',
              type: 'warning',
            });
            return
          }
          this.isInput = !this.isInput;
          if (!this.isInput){
            const query = Bmob.Query('_User');
            query.get(this.returnUser.objectId).then(res => {
              let arr = ['Introduction','phone','diZhi','biaoqian','img'];
              arr.forEach((item) => {
                if (this.returnUser[item]){
                  res.set(item,this.returnUser[item]);
                }
              });
              res.save();
              this.$store.commit('setUser',this.returnUser);
              this.$message({
                message: '修改成功！',
                type: 'success',
              });
            }).catch(err => {
              console.log(this.returnUser)
              this.$message({
                message: err.message,
                type: 'error',
              })
            })
          }
        },
        clickImg(img,index){
          this.imgsrc = img;
          let allLi = this.$refs.ul.querySelectorAll('li');
          for (let i=0; i<allLi.length; i++){
            allLi[i].classList.remove('active');
          }
          allLi[index].classList.add('active');
        },
        queDinImg(){
          this.imgOpenShow = !this.imgOpenShow;
          if (this.imgsrc){
            this.returnUser.img = this.imgsrc;
          }
        },
        xiuGaiClick(){
          this.imgOpenShow = !this.imgOpenShow;
          this.imgsrc = '';
          this.$nextTick(function () {
            var windowHeight = document.documentElement.clientHeight;
            var height = this.$refs.imgOpen.offsetHeight;
            this.$refs.imgOpen.style.top = (windowHeight - height)/2 + 'px';
          })
        },
        addBiaoQian(){
          if (!this.returnUser.biaoqian) {
            this.returnUser.biaoqian = [];
          }
          this.returnUser.biaoqian.push(this.biaoqian);
          this.biaoqian = '';
        },
        deleteBiaoQian(index){
          this.returnUser.biaoqian.splice(index,1);
          this.$forceUpdate();
        },
      },
      created(){
          let user = Bmob.User.current();
          this.$store.commit('setUser',user);
      },
      computed:{
          returnUser(){
            let user = this.$store.getters.getUser;
            if (user){
              let newUser = Object.assign({}, user);
              return newUser;
            } else {
              return '登录后可查看和修改信息'
            }
          },
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.bigBox{
  height: 100%;
  padding: 25px;
  .userInformation{
    width: 100%;
    height: 100%;
    overflow: scroll;
    .border();
    background-color: #fff;
    .left{
      height: 100%;
      text-align: center;
      padding: 10% 0 30px 0;
      border-right: 1px solid #f0f1f3;
      .xiuGaiTouXiang{
        display: inline-block;
        padding-top: 8px;
        color: #409EFF;
        cursor: pointer;
      }
      .userInput{
        padding: 10px 10%;
      }
      .img{
        width: 85px;
        height: 85px;
        margin: 0 auto;
        border: 8px solid #e6ecf1;
        border-radius: 50%;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
      h6{
        padding-top: 10px;
        font-size: 18px;
        color: #131e22;
      }
      p{
        color: #33484f;
        padding: 10px 20px 20px;
      }
      .el-button{
        padding: 9px 25px;
        box-shadow: 0 2px 4px #0099ff47;
      }
    }
    .right{
      height: 100%;
      .userInput{
        padding: 5px 0!important;
      }
      .box{
        height: 33.33%;
        border-bottom: 1px solid #f0f1f3;
        color: #667f87;
        overflow: scroll;
        padding: 4% 0 20px;
        .biaoQianInput{
          width: 40%;
          float: right;
        }
        .el-row{
          padding: 20px 30px 15px;
          font-size: 16px;
          line-height: 30px;
        }
        .el-col{
          padding: 0 30px 10px;
          span{
            color: #33484f;
            display: inline-block;
            padding-bottom: 5px;
          }
          .biaoQian{
            display: inline-block;
            padding: 2px 3%;
            border-radius: 50px;
            color: #fff;
            background-color: #d1d6da;
            margin-right: 3%;
            margin-bottom: 10px;
            transition: all .5s;
            span{
              margin-left: 10px;
              line-height: 20px;
              padding-bottom: 0;
              cursor: pointer;
              transition: all .5s;
              color: #999;
              &:hover{
                color: #666;
              }
            }
          }
        }
      }
    }
  }
  .xiuGaiOpen{
    .openBj();
    .xiuGaiBox{
      .openBox();
      width: 700px;
      text-align: center;
      padding-bottom: 30px;
      position: absolute;
      left: calc(~"(50% - 350px)");
      margin: 0;
      p{
        font-size: 18px;
        color: #667f87;
        padding-top: 25px;
      }
      ul{
        width: 83%;
        padding: 30px 0 10px;
        overflow: hidden;
        display: inline-block;
        .active{
          border-color: #409EFF;
        }
        li{
          width: 80px;
          height: 80px;
          border: 8px solid #e6ecf1;
          border-radius: 50%;
          float: left;
          margin: 0 10px 30px;
          cursor: pointer;
          transition: all 1s;
          &:hover{
            border-color: #409EFF;
          }
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .el-button{
        padding: 9px 30px;
      }
    }
  }
}
.slide-enter-active, .slide-leave-active {
  transition: all .5s ease;
}
.slide-enter, .slide-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(10px);
}
.fade-enter-active, .fade-leave-active {
  transition: all .5s ease;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>



