<template>
  <router-view></router-view>
</template>

<script>
    export default {
        name: "blog",
      beforeRouteLeave(to, from, next) {
        this.$store.getters.getBlog.load = true;
        next()
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";

</style>



