<template>
  <el-col :span="4" class="tab">
    <ul>
      <router-link :to="{name:'keshihua'}" tag="li">
        <i class="el-icon-menu"></i>
        <span>图表</span>
      </router-link>
      <router-link :to="{name:'table'}" tag="li">
        <i class="el-icon-location"></i>
        <span>表格</span>
      </router-link>
      <router-link :to="{name:'user'}" tag="li">
        <i class="el-icon-document"></i>
        <span>个人信息</span>
      </router-link>
      <li>
        <i class="el-icon-setting"></i>
        <span>导航四</span>
      </li>
    </ul>
  </el-col>
</template>

<script>
    export default {
        name: "backEndTab"
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
  .tab{
    height: 100%;
    background-color: #fff;
    ul{
      border-right: 1px solid @contourColor;
      padding: 10px;
      height: calc(~"(100% - 20px)");
      .active{
        background-color: @fuColor;
        border-radius: 5px;
        color: @mainColor;
        i{
          color: @mainColor;
        }
      }
      li{
        padding: 16px 10px 16px 25px;
        cursor: pointer;
        transition: all .3s;
        color: #303133;
        i{
          color: #909399;
        }
        &:hover{
          color: @mainColor;
          i{
            transition: all .3s;
            color: @mainColor;
          }
        }
      }
    }
  }
</style>



