<template>
    <el-col :span="6" class="left">
      <div class="fenlei">
        <ul>
          <li v-for="(item,index) in returnBlogFenLei" :key="index" @click="clickItem(item)" :class="(item.active?'active':'')">{{item.title}}</li>
        </ul>
        <el-button type="primary" size="small" round @click="addBlog">新建博客</el-button>
      </div>
    </el-col>
</template>

<script>
    export default {
        name: "blogFenLei",
      data(){
          return {

          }
      },
      computed:{
          returnBlogFenLei(){
            return this.$store.getters.getBlogFenLei;
          },
      },
      methods:{
        addBlog(){
          this.$router.push({name:'newBlog'})
        },
        clickItem(item){
          this.returnBlogFenLei.forEach((one) => {
            one.active = false;
          });
          item.active = true;
          if (item.type === '0'){
            const query = Bmob.Query("blog");
            this.$store.commit('setBlogLoading',true);
            query.find().then(res => {
              res.load = false;
              this.$store.commit('setBlog',res.reverse());
              this.$store.commit('setBlogLoading',false);
            });
          } else if(item.type === '1'){
            const query = Bmob.Query("blog");
            this.$store.commit('setBlogLoading',true);
            query.containedIn("username", [this.$store.getters.getUser.username]);
            query.find().then(res => {
              res.load = false;
              this.$store.commit('setBlog',res.reverse());
              this.$store.commit('setBlogLoading',false);
            });
          }

        },
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.left{
  height: 100%;
  .fenlei{
    background-color: #fff;
    .border();
    text-align: center;
    ul{
      padding: 10px;
      color: #131e22;
      .active{
        color: @mainColor;
        &:after{
          width: 50%;
          left: calc(50% - (50%/2));
        }
      }
      li{
        line-height: 40px;
        margin-bottom: 10px;
        cursor: pointer;
        position: relative;
        transition: color .5s;
        &:after{
          content: '';
          width: 0;
          height: 2px;
          position: absolute;
          left: 50%;
          border-radius: 10px;
          transition: all .5s;
          bottom: 0;
          background-color: @mainColor;
        }
        &:hover{
          &:after{
            width: 50%;
            left: calc(50% - (50%/2));
          }
        }
      }
    }
    .el-button{
      display: inline-block;
      width: 80%;
      margin: 0 auto 20px;
    }
  }
}
</style>



