
//一级路由
import backEndModule from '../../components/backEndModule/backEndModule'
import projectsShow from '../../components/ProjectsShow/ProjectsShow'
import personalResume from '../../components/PersonalResume/PersonalResume'
import blog from '../../components/blog/blog'

//后台模块二级路由引入
import shuJuKeShiHua from '../../components/backEndModule/shuJuKeShiHua'
import tableComponent from '../../components/backEndModule/tableComponent'
import userInformation from '../../components/backEndModule/userInformation'

//博客模块二级路由引入
import newBlog from '../../components/blog/newBlog'
import blogContent from '../../components/blog/blogContent'
import blogDetails from '../../components/blog/blogDetails'

export const routes = [
  {path:'/backend',name:'backend',component:backEndModule,redirect:'/backend/keshihua',children:[
      {path:'/backend/keshihua',name:'keshihua',component:shuJuKeShiHua},
      {path:'/backend/table',name:'table',component:tableComponent},
      {path:'/backend/user',name:'user',component:userInformation},
    ]},
  {path:'/projects',name:'projects',component:projectsShow},
  {path:'/personal',name:'personal',component:personalResume},
  {path:'/blog',name:'blog',component:blog,redirect:'/blog/content',children:[
      {path:'/blog/newBlog',name:'newBlog',component:newBlog},
      {path:'/blog/content',name:'content',component:blogContent},
      {path:'/blog/details',name:'details',component:blogDetails},
    ]},
  {path:'*',redirect:'/backend'}
];



