<template>
    <el-col class="box">
      <v-chart :forceFit="true" :height="height" :data="data" :padding="[40,30,70,50]">
        <v-tooltip />
        <v-axis />
        <v-legend />
        <v-bar position="月份*月均降雨量" color="name" :adjust="adjust" />
      </v-chart>
    </el-col>
</template>

<script>
  const DataSet = require('@antv/data-set');

  const sourceData = [
    { name: 'London', 'Jan.': 18.9, 'Feb.': 28.8, 'Mar.': 39.3, 'Apr.': 81.4, 'May': 47, 'Jun.': 20.3, 'Jul.': 24, 'Aug.': 35.6 },
    { name: 'Berlin', 'Jan.': 12.4, 'Feb.': 23.2, 'Mar.': 34.5, 'Apr.': 99.7, 'May': 52.6, 'Jun.': 35.5, 'Jul.': 37.4, 'Aug.': 42.4 },
  ];

  const dv = new DataSet.View().source(sourceData);
  dv.transform({
    type: 'fold',
    fields: ['Jan.', 'Feb.', 'Mar.', 'Apr.', 'May', 'Jun.', 'Jul.', 'Aug.'],
    key: '月份',
    value: '月均降雨量',
  });
  const data = dv.rows;
  export default {
        name: "fenZuZhuTu",
    data(){
          return {
            data,
            height: 300,
            adjust: [{
              type: 'dodge',
              marginRation: 1/32,
            }],
          }
      },
    mounted(){
      let height = document.querySelector('body').clientHeight - 208;
      if (height > 280){
        this.height = 280;
      } else {
        this.height = height;
      }
    },
    }
</script>

<style scoped lang="less">
@import "../../assets/css/less";
.box{
  margin: 0 1.5% 2% 0;
  background-color: #fff;
  width: 47.75%;
  max-height: 290px;
  .border();
}
</style>



