<template>
    <div class="window">
      <div class="box">
        <div class="box-header">标题<span class="el-icon-close"></span></div>
        <div class="box-main">
          <el-input placeholder="姓名"></el-input>
          <el-input placeholder="地址"></el-input>
          <el-input placeholder="电话"></el-input>
          <el-date-picker
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </div>
        <div class="box-bottom">
          <el-button type="primary">确定</el-button>
          <el-button>取消</el-button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "openWindow",
      data(){
          return {
            isShow: false,
          }
      },
    }
</script>

<style scoped lang="less">
.window{
  width: 100%;
  height: 100%;
  background-color: rgba(11,28,60,.2);
  position: fixed;
  top: 0;
  left: 0;
  .box{
    width: 30%;
    height: auto;
    margin: 4% auto;
    border-radius: 6px;
    box-shadow: 0 2px 15px 2px rgba(3,29,78,.1);
    background-color: #fff;
    .box-header{
      padding: 18px 20px;
      color: #33484f;
      font-size: 16px;
      line-height: 16px;
      span{
        float: right;
        cursor: pointer;
        font-size: 18px;
        color: #75939e;
      }
    }
    .box-main{
      padding: 5px 20px;
      .el-input{
        color: #33484f;
        margin: 6px 0;
        width: 100%;
      }
    }
    .box-bottom{
      padding: 15px 20px 25px 20px;
      button{
        padding: 10px 25px;
      }
    }
  }
}
</style>



