<template>
    <div class="header">
      <el-col :span="2" class="logo"><span><img src=".././assets/img/logo.png"></span>LOGO</el-col>
      <el-header>
        <transition name="fade" mode="out-in">
          <ul>
            <router-link :to="{name:'backend'}" tag="li"><el-col :span="2">后台模块</el-col></router-link>
            <router-link :to="{name:'projects'}" tag="li"><el-col :span="2">其它组件</el-col></router-link>
            <router-link :to="{name:'personal'}" tag="li"><el-col :span="2">其它组件</el-col></router-link>
            <router-link :to="{name:'blog'}" tag="li"><el-col :span="2">博客组件</el-col></router-link>
          </ul>
        </transition>
        <user></user>
      </el-header>
    </div>
</template>

<script>
  import User from './user/user'
    export default {
        name: "Header",
      components:{
          User,
      },
      data(){
          return {
            activeIndex: '1',
          }
      }
    }
</script>

<style scoped lang="less">
@import ".././assets/css/less";
.header{
  border-bottom: solid 1px @contourColor;
  position: relative;
  height: 60px;
  overflow: hidden;
  .logo{
    background-color: @mainColor;
    height: 100%;
    color: #fff;
    line-height: 60px;
    font-size: 18px;
    text-align: center;
    position: relative;
    min-width: 110px;
    max-width: 120px;
    &:after{
      content: '';
      width: 80px;
      height: 80px;
      border-radius: 20px;
      background-color: @mainColor;
      position: absolute;
      top: -27px;
      left: 83px;
      transform: rotate(-30deg);
      z-index: -1;
    }
    span{
      display: inline-block;
      width: 20px;
      height: 20px;
      margin-right: 5px;
      vertical-align: -5%;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .el-header{
    padding-left: 180px;
  }
  ul{
    display: inline;
    overflow: hidden;
    transition: all 3s;
    .active{
      color: @mainColor;
      div{
        position: relative;
        transition: all .5s;
        &:after{
          transition: all .8s;
          opacity: 1;
        }
        &:hover{
          color: @mainColor;
        }
      }
    }
    li{
      text-align: center;
      line-height: 60px;
      color: #888;
      div{
        position: relative;
        cursor: pointer;
        transition: all .5s;
        &:hover{
          color: #333;
        }
        &:after{
          content: '';
          width: 80%;
          height: 2px;
          background-color: @mainColor;
          border-radius: 20px;
          position: absolute;
          opacity: 0;
          bottom: 0;
          left: 50%-(80%/2);
        }
      }
    }
  }
}
.fade-enter-active, .fade-leave-active {
  transition: all 5s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



