<template>
    <el-col :span="20" class="box" v-loading="loading">
      <el-row class="top">
        <div class="left">
          <h5 class="left">表格标题</h5>
        </div>
        <div class="right">
          <add-open @addClick="requestTable"></add-open>
          <div>修改</div>
        </div>
      </el-row>
      <table class="table">
        <thead>
        <tr>
          <td>姓名</td>
          <td>地址</td>
          <td>电话</td>
          <td>日期</td>
          <td>操作</td>
        </tr>
        </thead>
        <tbody class="tbody">
        <tr v-for="(item,index) in returnTable" :key="index">
          <td>{{item.name}}</td>
          <td>{{item.address}}</td>
          <td>{{item.phone}}</td>
          <td>{{item.date}}</td>
          <td>
            <span class="big" @click="modify(item)">修改</span>
            <span class="el-icon-edit xiao" @click="modify(item)"></span>
            <span class="delete big" @click="deleteItem(item)">删除</span>
            <span class="el-icon-delete xiao delete" @click="deleteItem(item)"></span>
          </td>
        </tr>
        </tbody>
      </table>
      <el-row class="page">
        <el-pagination
          :page-size="size"
          :pager-count="11"
          layout="total,prev, pager, next"
          :total="returnTabLength"
          @next-click="nextPrev"
          @prev-click="nextPrev"
          @current-change="nextPrev">
        </el-pagination>
      </el-row>
      <transition name="fade">
        <div class="window" v-show="isShow" @click="modify">
          <transition name="slide">
            <div class="box" v-show="isShow" @click.stop="">
              <div class="box-header">修改信息<span class="el-icon-close" @click="modify"></span></div>
              <div class="box-main">
                <el-input placeholder="姓名" v-model="form.name"></el-input>
                <el-input placeholder="地址" v-model="form.address"></el-input>
                <el-input placeholder="电话" v-model="form.phone"></el-input>
                <el-date-picker
                  v-model="form.date"
                  type="date"
                  placeholder="选择日期"
                  value-format="yyyy-MM-dd">
                </el-date-picker>
              </div>
              <div class="box-bottom">
                <el-button type="primary" @click="confirm">确定</el-button>
                <el-button @click="modify">取消</el-button>
              </div>
            </div>
          </transition>
        </div>
      </transition>
      <transition name="fade">
        <div class="window" v-show="isDeleteShow" @click="deleteItem">
          <transition name="slide">
            <div class="deleteBox" v-show="isDeleteShow" @click.stop="">
              <div class="box-header">确认信息<span class="el-icon-close" @click="deleteItem"></span></div>
              <div class="box-main">确认删除本条信息吗？</div>
              <div class="box-bottom">
                <el-button type="primary" @click="confirmDelete">确认</el-button>
                <el-button @click="deleteItem">取消</el-button>
              </div>
            </div>
          </transition>
        </div>
      </transition>
    </el-col>
</template>

<script>
  import addOpen from './addOpen'
  import openWindow from './openWindow'
    export default {
        name: "tableComponent",
      components:{
          'add-open':addOpen,
          'open-window':openWindow,
      },
      data(){
          return {
            tableng: '',
            loading: true,
            current: 0,
            size: 10,
            isShow: false,
            isDeleteShow: false,
            form:{
              name:'',
              address:'',
              phone:'',
              date:'',
            },
            form2:{},
            deleteForm:{},
          }
      },
      created(){
        //请求table数据
        this.requestTable();
      },
      computed:{
          returnTable(){
            return this.$store.getters.getTable;
          },
        returnTabLength(){
            return this.$store.getters.getTabLength;
        },
      },
      mounted(){
        //设置tbody高度
        let tbody = document.querySelector('.tbody');
        let height = document.querySelector('body').clientHeight;
        tbody.style.height = height - 233 + 'px';
      },
      methods:{
          // 分页上一页，下一页点击事件
          nextPrev(page){
            this.current = (page - 1) * 10;
            this.requestTable();
          },
          //修改点击事件
          modify(item){
            this.isShow = !this.isShow;
            this.form = Object.assign({},item);
            this.form2 = Object.assign({},this.form);
          },
          //删除按钮点击
          deleteItem(item){
            this.isDeleteShow = !this.isDeleteShow;
            this.deleteForm = item;
          },
          //删除弹出确定按钮事件
          confirmDelete(){
            const query = Bmob.Query('table');
            query.destroy(this.deleteForm.objectId).then(res => {
              this.$message({
                showClose: true,
                message: '删除成功！',
                type: 'success',
                duration: 1000,
              });
              this.isDeleteShow = false;
              this.requestTable();
            }).catch(err => {
              console.log(err)
            })
          },
          //确认按钮点击事件
          confirm(){
            for (let i in this.form){
              for (let j in this.form2){
                if (i === j){
                  if (this.form[i] !== this.form2[j]){
                    const query = Bmob.Query('table');
                    query.get(this.form.objectId).then(res => {
                      res.set('name',this.form.name);
                      res.set('address',this.form.address);
                      res.set('phone',this.form.phone);
                      res.set('data',this.form.date);
                      res.save();
                      this.$message({
                        showClose: true,
                        message: '修改成功！',
                        type: 'success',
                        duration: 1000,
                      });
                      this.requestTable();
                    }).catch(err => {
                      console.log(err)
                    });
                    this.isShow = false;
                    return;
                  }
                }
              }
            }
            this.isShow = false;
          },
          //请求table数据方法
          requestTable(){
            this.loading = true;
            const query = Bmob.Query("table");
            query.limit(this.size);
            query.skip(this.current);
            query.find().then(res => {
              if (res.length <= 0){
                this.current -= this.size;
                this.requestTable();
                return;
              }
              res.forEach((item) => {
                for (let i in item){
                  if (i === 'date'){
                    item[i] = item[i].substring(0,10)
                  }
                }
              });
              this.$store.commit('setTable',res);
              this.loading = false;
            }).catch(err => {
              console.log(err)
            });
            query.count().then(res => {
              this.$store.commit('setTabLength',res);
            });
          }
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.box{
  .top{
    padding: 20px;
    background-color: #eff5f7;
    border-bottom: 1px solid @contourColor;
  }
  .page{
    padding: 5px 10px;
    background-color: #fff;
    border-top: 1px solid @contourColor;
    border-bottom: 1px solid @contourColor;
    .el-pagination{
      color: #667f87;
    }
  }
  .left{
    float: left;
    h5{
      color: #131e22;
      font-weight: 500;
      line-height: 37px;
    }
    p{
      font-size: 12px;
      line-height: 12px;
      margin-top: 22px;
      color: #91a8b0;
    }
  }
  .right{
    float: right;
    div{
      &:nth-child(2){
        float: left;
        cursor: pointer;
        margin-right: 15px;
        padding: 8px 30px;
        border-radius: 5px;
        transition: all .8s;
        background-color: #fff;
        color: #667f87;
        box-shadow: 0 2px 4px #aaadaf47;
        &:hover{
          background-color: #f8fafb;
        }
      }
    }
  }
  .table{
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: block;
    td{
      padding-left: 25px;
      width: 100%/5;
      font-weight: 400;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      .xiao{
        display: none;
        @media (max-width: 1000px){
          display: inline-block;
          padding: 8px;
        }
      }
      .big{
        @media (max-width: 1000px){
          display: none;
        }
      }
      span{
        border-radius: 100px;
        border: none;
        cursor: pointer;
        padding: 8px 13%;
        background-color: #53b3fd;
        color: #fff;
        transition: all .8s;
        font-size: 12px;
        &:hover{
          background-color: #39a0f1;
        }
      }
      .delete{
        margin-left: 10px;
        background-color: #ffc060;
        &:hover{
          background-color: #f9b144;
        }
      }
    }
    thead{
      line-height: 50px;
      background-color: #eff5f7;
      color: #91a8b0;
      font-weight: 500;
      border-bottom: 1px solid @contourColor;
      display: table;
      width: 100%;
    }
    tbody{
      transition: all .8s;
      display: block;
      overflow: scroll;
      width: 100%;
      tr{
        display: table;
        width: 100%;
        line-height: 60px;
        background-color: #fff;
        color: #33484f;
        font-weight: 300;
        border-bottom: 1px solid #eff5f7;
        &:nth-of-type(even){
          background-color: #f8fbfc;
        }
        &:hover{
          background-color: #ebf7ff;
        }
      }
    }
  }
  .window{
    width: 100%;
    height: 100%;
    background-color: rgba(11,28,60,.2);
    position: fixed;
    top: 0;
    left: 0;
    .deleteBox,
    .box{
      .openBox();
      .box-header{
        .openHeader();
      }
      .box-main{
        .openMain();
      }
      .box-bottom{
        .openBottom();
      }
    }
  }
}
.slide-enter-active, .slide-leave-active {
  transition: all .5s ease;
}
.slide-enter, .slide-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(10px);
}
.fade-enter-active, .fade-leave-active {
  transition: all .5s ease;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>



