<template>
    <div class="addproject">
      <div class="add el-icon-plus" @click="clickAdd"></div>
      <transition name="fade">
        <div class="diBox" v-show="isShow" @click="isShow = !isShow">
          <transition name="slide">
            <div class="box" v-show="isShow" @click.stop="">
              <div class="title">
                <h6>添加新项目</h6>
              </div>
              <el-form>
                <el-form-item prop="title">
                  <el-input placeholder="请输入标题" v-model="newProject.title" clearable></el-input>
                </el-form-item>
                <el-form-item>
                  <el-input placeholder="请输入链接地址" v-model="newProject.url" clearable></el-input>
                </el-form-item>
                <el-form-item>
                  <el-input placeholder="Github" v-model="newProject.github" clearable></el-input>
                </el-form-item>
                <el-form-item>
                  <el-input type="textarea" placeholder="请输入简介" v-model="newProject.jianJie" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
                <el-form-item label="选择显示客户端：" class="kehuduan">
                  <span v-for="(item,index) in kehuduan" :key="index" :class="{'active':item.active}" @click="clickKeHuDuan(item)">{{item.kehuduan}}</span>
                </el-form-item>
              </el-form>
              <div class="button">
                <el-button size="small" @click="isShow = !isShow">取消</el-button>
                <el-button type="primary" size="small" @click="addProject">确定</el-button>
              </div>
            </div>
          </transition>
        </div>
      </transition>
    </div>
</template>

<script>
    export default {
        name: "addProject",
      data(){
          return {
            kehuduan: [
              {kehuduan: 'Web', active: false},
              {kehuduan: 'Phone', active: false},
            ],
            newProject: {
              title: '',
              url: '',
              jianJie: '',
              keHuDuan: '',
              github: '',
            },
            isShow: false,
          }
      },
      methods:{
        clickKeHuDuan(item){
          this.kehuduan.forEach((ke) => {
            ke.active = false;
          });
          this.newProject.keHuDuan = item.kehuduan;
          item.active = true;
        },
        clickAdd(){
          let user = this.$store.getters.getUser;
          if (user.permissions === 'admin'){
            this.isShow = !this.isShow;
          } else {
            this.$message({
              message: '您没有添加权限！',
              type: 'warning',
              duration: 2000,
            });
          }
        },
        addProject(){
          let newProject = ['title','url','jianJie','keHuDuan','github'];
          const query = Bmob.Query('project');
          newProject.forEach((item) => {
            if (item === 'jianJie') {
              let jianjie = this.newProject['jianJie'].replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, ' ');
              query.set(item,jianjie)
            } else {
              query.set(item,this.newProject[item])
            }
          });
          query.save().then(res => {
            this.$message({
              message: '添加成功！',
              type: 'success',
            });
            this.isShow = !this.isShow;
            this.newProject = '';
            this.$emit('getProject')
          }).catch(err => {
            this.$message({
              message: err.error,
              type: 'error',
            })
          })
        },
      },
    }
</script>

<style scoped lang="less">
  @import "../../assets/css/less";
.addproject{
  .diBox{
    .openBj();
    overflow: scroll;
    .box{
      .openBox();
      padding: 20px 30px;
      .title{
        text-align: center;
        padding-bottom: 20px;
        h6{
          color: #4e6269;
          font-size: 16px;
          font-weight: 500;
        }
      }
      .kehuduan{
        margin-bottom: 0;
        span{
          padding: 4px 12px;
          border-radius: 2px;
          background-color: #ccc;
          color: #fff;
          margin-right: 15px;
          cursor: pointer;
        }
        .active{
          background-color: @mainColor;
        }
      }
      .button{
        margin-top: 20px;
        text-align: center;
        button{
          padding: 9px 25px;
        }
      }
    }
  }
  .add{
    background-color: @mainColor;
    color: #fff;
    padding: 10px;
    font-size: 18px;
    position: fixed;
    top: 80px;
    right: 45px;
    cursor: pointer;
    border-radius: 50px;
    transition: all .5s;
    box-shadow: 0 2px 10px 2px rgba(0,153,255,.3);
    &:hover{
      box-shadow: 0 2px 18px 3px rgba(0,153,255,.6);
    }
  }
}
  .slide-enter-active, .slide-leave-active {
    transition: all .5s ease;
  }
  .slide-enter, .slide-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(10px);
  }
  .fade-enter-active, .fade-leave-active {
    transition: all .5s ease;
  }
  .fade-enter, .fade-leave-to{
    opacity: 0;
  }
</style>



